body {
  margin: 0;
  box-sizing: border-box !important;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.active{
  background-color: rgb(104, 198, 104);
}
.pending{
  background-color: rgb(230, 230, 64);
}
.passive{
  background-color: rgb(236, 69, 69);
}
::-webkit-scrollbar{
  width: 8px;
}
::-webkit-scrollbar-thumb{
  background-color: #904ece;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover{
  background-color: #8633d4;
  cursor: pointer !important;
}
::-webkit-scrollbar-corner{
  background-color: transparent;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}
.cover{
  object-fit: cover;
}
.customShadow{
  -webkit-box-shadow: 2px 2px 4px 1px rgba(185, 190, 198, 0.6);
-moz-box-shadow: 2px 2px 4px 1px rgba(185, 190, 198, 0.6);
box-shadow: 2px 2px 4px 1px rgba(185, 190, 198, 0.6);
}
.icon{
  border-radius: 3px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
